#app-root {
  height: 10em;
  width: 10em;
  background: rgb(1, 18, 24);
  overflow: auto;
}

#modal-root {
  position: relative;
  z-index: 999;

}

.modal {
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  overflow-x: hidden;
}


